<template>
  <div class="container">
    <div class="screen">
      <div class="screen__content">
        <form class="login" action="" id="frm" ref="frm">
          <h1 style="color: #7875B5; ">회원 가입</h1>
          <div class="login__field">
            <input type="email" class="login__input " ref="email" v-model="state.email" placeholder="이메일"
              @keyup="emailCheckHandler" autofocus>
            <label> {{ state.emailCheck }}</label>
          </div>
          <div class="login__field">
            <input type="text" class="login__input" ref="nickname" v-model="state.nickname" placeholder="닉네임"
              maxlength="10">
          </div>
          <div class="login__field">
            <input type="password" class="login__input" placeholder="비밀번호" ref="password" v-model="state.password">
          </div>
          <div class="login__field">
            <input type="password" class="login__input" placeholder="비밀번호 확인" ref="repassword"
              v-model="state.repassword">
          </div>
          <div class="login__field">
            <input type="text" class="login__input" ref="secretQ" v-model="state.secretQ" placeholder="나만의 질문">
          </div>
          <div class="login__field">
            <input type="text" class="login__input" ref="secretA" v-model="state.secretA" placeholder="나만의 답변">
          </div>
          <button class="button login__submit" @click.prevent="joinHandler">
            <span class="button__text">지금 가입하기
              &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp>></span>
          </button>
        </form>
      </div>
      <div class="screen__background">
        <span class="screen__background__shape screen__background__shape4"></span>
        <span class="screen__background__shape screen__background__shape3"></span>
        <span class="screen__background__shape screen__background__shape2"></span>
        <span class="screen__background__shape screen__background__shape1"></span>
      </div>
    </div>
  </div>
  <Teleport to="body">
    <modal2 :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h2>
          <div class="snowflakes" aria-hidden="true">
            <div class="snowflake"> ❅ </div>
            <div class="snowflake"> ❅ </div>
            <div class="snowflake"> ❆ </div>
            <div class="snowflake"> ❄ </div>
            <div class="snowflake"> ❅ </div>
            <div class="snowflake"> ❆ </div>
            <div class="snowflake"> ❄ </div>
            <div class="snowflake"> ❅ </div>
            <div class="snowflake"> ❆ </div>
            <div class="snowflake"> ❄ </div>
          </div>
        </h2>
      </template>
      <template #body>
        <img style="width:55em" alt="user icon" src="../assets/images/pictures/congraturations3.jpg">
        <div>
          <h2>회원가입이 성공적으로 되었습니다.</h2>
        </div>
      </template>
      <template #footer>
        <el-button type="primary" plain @click="toLogin">로그인</el-button>
        <el-button type="primary" plain @click="toMain">Thank you</el-button>
      </template>
    </modal2>
  </Teleport>
</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import UserProfilePic from './UserProfilePic.vue';
import Modal2 from './teleport/Modal2.vue'
export default {
  name: "TheSingup",
  setup() {
    const router = useRouter();
    const state = reactive({
      email: "",
      nickname: "",
      password: "",
      repassword: "",
      secretA: "",
      secretQ: "",
      emailCheck: "",
      uniqueMail: true,
      showModal: false,
      token: sessionStorage.getItem("TOKEN"),
    });
    const email = ref("");
    const password = ref("");
    const repassword = ref("");
    const nickname = ref("");
    const secretQ = ref("");
    const secretA = ref("");
    const exptext = /^[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-Za-z0-9\-]+/;
    const pattern = /^\s+$/;
    const emailCheckHandler = async () => {
      //TODO: a value set in the comp can be alter in the function at setup
      //
      const url = "http://43.201.60.125/signup/emailCheck";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = { email: state.email };
      const response = await axios.post(url, body, { headers });
      if (response.data.result === 1) {
        state.uniqueMail = false;
        console.log("uniqueMail :" + state.uniqueMail);
        state.emailCheck = "이미 등록된 이메일 입니다";
      }
      else {
        state.emailCheck = "사용가능";
        state.uniqueMail = true;
        console.log("uniqueMail :" + state.uniqueMail);
      }
      console.log("state.emailCheck: " + state.emailCheck);
      //TODO: make an error handler
    };
    const joinHandler = async () => {
      if (state.email === "") {
        alert("Please Input your email");
        email.value.focus();
        return false;
      }
      else if (state.uniqueMail === false) {
        alert("you can t use that mail");
        email.value.focus();
        return false;
      }
      else if (state.nickname === "") {
        alert("Check nickname");
        nickname.value.focus();
        return false;
      }
      else if (state.password === "") {
        alert("Check Password");
        password.value.focus();
        return false;
      }
      else if (state.repassword === "") {
        alert("Check RePassword");
        repassword.value.focus();
        return false;
      }
      else if (state.secretQ === "") {
        alert("Input your secret question");
        secretQ.value.focus();
        return false;
      }
      else if (state.secretA === "") {
        alert("Input your secret answer");
        secretA.value.focus();
        return false;
      }
      else if (state.password != state.repassword) {
        alert("Password doesn't match");
        password.value.value = "";
        repassword.value.value = "";
        password.value.focus();
        return false;
      }
      else if (exptext.test(state.email) == false) {
        alert("이메일 형식이 올바르지 않습니다");
        email.value.focus();
        return false;
      }
      else if (pattern.test(state.password)) {
        alert("space is not allowed");
        password.value.value = "";
        password.value.focus();
        return false;
      }
      const url = "http://43.201.60.125/signup/userRegister";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      };
      const body = {
        email: state.email,
        password: state.password,
        nickname: state.nickname,
        secretQ: state.secretQ,
        secretA: state.secretA,
      };
      const response = await axios.post(url, body, { headers });
      console.log(response.data);
      if (response.status === 200) {
        // alert("회원가입이 되었습니다.");
        state.showModal = true;
      }
      else {
        alert("회원가입에 실패하였습니다.");

      }
    };
    const toLogin = () => {
      router.push({ name: "login" });
      state.showModal = false
    }
    const toMain = () => {
      router.push({ name: "home" });
      state.showModal = false
    }
    return { state, email, password, secretA, nickname, secretQ, repassword, emailCheckHandler, joinHandler, exptext, pattern, toLogin, toMain };
  },
  data() {
    return {};
  },
  components: { UserProfilePic, Modal2 }
};
</script>
<style scoped>
/* * {
  box-sizing: border-box;
